.homepage_section {
  font-family: "Segoe UI", sans-serif;
  line-height: 1.5;
  margin-top: -150px;
}

.presentation {
  width: fit-content;
  box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  padding: 5%;
  margin-left: 70%;
  transition: all 0.5s ease;
}

.presentation:hover {
  transform: scale(1.5);
  box-shadow: 0px 15px 40px 15px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.welcome {
  font-size: 50px;
  font-weight: 600;
  margin-bottom: 10%;
}

.name {
  font-size: 30px;
}

